import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { PictProvider } from '../../providers/pict/pict';
import { Geolocation } from '@ionic-native/geolocation';
import { Network } from '@ionic-native/network';
import { dateValueRange } from 'ionic-angular/umd/util/datetime-util';

/**
 * Generated class for the LocationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-location',
  templateUrl: 'location.html',
})
export class LocationPage {

  dateArr : string[] = ['January','February','March','April','May','June','July','August','September','October','November','December'];
  formattedDate;
  currentdate : any;
  pic : any;
  lat : any;
  lon : any;
  status : any;
  ntype : any;
  safari = 'Network';

  items: any = {
    'Network': [
      {
        name: 'Status',
      },
      {
        name: 'Type',
      }
    ],
    'Location': [
     
      {
        name: 'Current Location'
      },
      {
        name: 'Lattitude'
      },
      {
        name: 'Longitude'
      }
    ],
  };

  constructor(public navCtrl: NavController, public navParams: NavParams, private pictProvider:PictProvider, public geolocation: Geolocation, public network: Network) {
    this.currentdate = new Date();
    this.getFormattedDate();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LocationPage');
       
this.network.onDisconnect().subscribe((data) => {
  console.log(data,"network was disconnected :-(");
  this.status = 'Disconnected';
  this.ntype = this.network.type;
});

// this.network.onchange().subscribe((data) => {
//   console.log(data,"ddddddddd");
//   console.log('network connected!');

//   // this.status = data.type=="online"?"connected":"disconnected";
//     this.ntype = this.network.type;
//     console.log(this.network.type,"ntype");
//     if (this.network.type === 'wifi') {
//       console.log('we got a wifi connection, woohoo!');
//     }
// });

this.network.onConnect().subscribe((data) => {
  this.status = 'Connected';
  this.ntype = this.network.type;
  console.log(data,"ddddddddd");
  console.log('network connecteddd!');
});
    this.pic=this.pictProvider.myPhoto;
    this.geolocation.getCurrentPosition().then( pos => {
      this.lat = pos.coords.latitude;
      this.lon = pos.coords.longitude;
    }).catch( err => console.log(err));
  }

  getFormattedDate()
  {
    var dateObj = new Date()

    var year = dateObj.getFullYear().toString()
    var month = dateObj.getMonth().toString()
    var date = dateObj.getDate().toString()

    this.formattedDate = date +' ' + this.dateArr[month] +' '+ year;
  }

  detailsDirect()
  {
    this.navCtrl.setRoot("DetailsPage");
  }

  getSafariItems(type: any) {
    return this.items[type];
  }

}



